@import "../../utilities";

.hide {
  opacity: 0;
  display: block;
  // transform: translateY(25%);
  // transform-origin: center;
  transition: all 0.2s ease-in-out;
}

section#home {
  padding: 0px 20px;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: $silver;
  font-family: $font-roboto;
  overflow-x: hidden;

  @include md-screen {
    padding: 0px 5%;
    // justify-content: space-evenly;
  }

  .container {
    width: 100%;
    max-width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: left;

    @include md-screen {
      text-align: center;
    }

    .hero {
      height: 50%;
      width: 100%;
      margin: auto;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;

      @include md-screen {
        height: 60%;
        align-items: center;
        justify-content: space-between;
      }

      &-intro {
        padding-top: 20px;
        padding-bottom: 30px;
        font-family: $font-lora;

        @include md-screen {
          padding: 0px;
        }

        h1 {
          font-size: 42px;
          line-height: 42px;
          letter-spacing: 1.2px;
          position: relative;
          display: block;

          @include md-screen {
            font-size: 48px;
            line-height: 48px;
          }
        }

        h3 {
          font-size: 48px;
          position: relative;
          display: block;
          z-index: 0;
          margin: 2.75rem 0rem 1rem;
          padding-left: 15px;
          font-weight: normal;

          @include md-screen {
            font-size: 72px;
            line-height: 72px;
          }

          &::after {
            content: url("../../assets/paint-stroke-title.svg");
            position: absolute;
            transform: translate(-5%, -40%) scaleX(0);
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: -1;
            // height: 100%;
            width: 130%;

            transform-origin: left;
            transition: all 0.4s linear;
          }

          &.paint::after {
            transform: translate(-5%, -40%) scaleX(1.4);
          }

          span {
            color: $silver;
          }
        }
      }

      &-details {
        p {
          font-size: 32px;
          line-height: 32px;
          padding-bottom: 10px;
        }

        div {
          font-size: 32px;
          display: block;
          line-height: 32px;
          justify-self: center;
          // max-width: 40%;

          @include md-screen {
            font-size: 38px;
            line-height: 38px;
          }

          span {
            line-height: 48px;
          }

          p {
            padding-top: 1rem;
            padding-right: 15px;
            font-size: 16px;
            line-height: 27px;
            letter-spacing: 1.25px;
            font-weight: 400;

            @include md-screen {
              padding-top: 1.5rem;
              padding-right: 0px;
              width: 60%;
              font-size: 20px;
            }
          }
        }
      }

      .cta {
        width: 100%;
        padding: 10px 0px;

        @include md-screen {
          width: 80%;
        }

        button {
          display: block;
          width: 100%;
          font-size: 24px;
          outline: none;
          padding: 10px;
          margin-top: 20px;
          text-transform: capitalize;
          cursor: pointer;
          transition: all 0.15s linear;

          &:first-child {
            margin-top: 10px;
          }

          @include md-screen {
          }
        }

        .btn-outline {
          border: 2px solid $silver;
          // border-width: 3px;
          background: transparent;
          color: $silver;

          &:hover,
          &:focus {
            background: $silver;
            color: $black;
          }
        }

        .btn-solid {
          background: transparent;
          border: 2px solid $bright-yellow;
          color: $silver;

          &:hover,
          &:focus {
            background: $bright-yellow;
            color: $black;
          }
        }
      }
    }
  }
}
